.inner {
  max-width: 1360px;
  margin: 0 auto;
  padding: 0 12px;
}

@media screen and (width >= 600px) {
  .inner {
    padding: 0 24px;
  }
}

.cart_header {
  padding-top: 140px;
}

@media screen and (width >= 768px) {
  .cart_header {
    padding-top: 220px;
  }
}

.cart_form div.grid {
  grid-template-columns: 1fr;
  gap: 12px;
}

@media screen and (width >= 768px) {
  .cart_form div.grid {
    grid-template-columns: 2fr 1fr;
    gap: 40px;
  }
}

@media screen and (width >= 1260px) {
  .cart_form div.grid {
    gap: 64px;
  }
}

@media screen and (width <= 768px) {
  .cart_form div.grid {
    padding: 0;
  }
}

.cart_form .cart_form-items {
  gap: 8px;
}

.cart_form .cart_form-item {
  border-bottom: 1px solid var(--color-tonal4);
  padding: 12px 0;
  position: relative;
}

.cart_form .cart_form-item:last-child {
  border-bottom: 0;
}

.cart_form button.go-cart__button {
  width: 100%;
}

.cart_form .cart_form-image {
  flex: 0 110px;
  max-width: 110px;
}

.cart_form .cart_form-text {
  flex: calc(100% - 120px);
  padding-left: 8px;
}

.cart_form .product-options li {
  opacity: .8;
  font-size: 81.25%;
}

.cart_form .x-till {
  padding-bottom: 16px;
  font-size: 87.5%;
}

.cart_form .summary {
  padding: 16px;
}

@media screen and (width >= 768px) {
  .cart_form .summary {
    padding: 24px;
  }
}

.cart_form .summary .totals p {
  border-top: 1px solid var(--color-tonal4);
  padding: 16px 0;
}

.cart_form .summary .button.nude {
  margin: 8px auto;
  font-weight: normal;
}

.cart_form .summary .accepted_methods {
  gap: 4px;
  padding: 8px 0 24px;
}

.cart_form .summary .accepted_methods li {
  flex: 1;
}

.cart_form .summary .accepted_methods li svg {
  max-height: 24px;
}

@media screen and (width >= 600px) {
  .cart_form .summary .accepted_methods li svg {
    max-height: 32px;
  }
}
/*# sourceMappingURL=styles-cart.css.map */
